import React, {Component} from 'react';
import Link from 'next/link';
import MenuList from './MenuList';

export default class NavBarMobile extends Component {

    state = {
        active: true,
        skrill: false,
        neteller: false,
        ecopayz: false,
        menu: true,
        icon: true,
        loggedIn: ''
    }

    
    toggleClass = () => {
        this.setState({
            active: !this.state.active
        })
    }       

    toggleMenu = () => {
        this.setState({
            menu: !this.state.menu,
            icon: !this.state.icon
        })
    }
   
    render () {

        // console.log(this.isLoggedIn())

        const linkStyle = {
            backgroundColor: '#003249', 
            color: 'white',
            textAlign: 'center'
        }

        const activeStyle = {
            backgroundColor: '#003249', 
            color: '#80CED7',
            textAlign: 'center'
        }
        

        return (
            <div 
                className='navbar'
                >
                <div 
                    className='navbar-brand' 
                    role="navigation" 
                    aria-label="main navigation"                    
                    >                    
                     <a href="/" style={{maxWidth: '150px', height: '100%', paddingTop: '1.5rem'}}>
                            <img src='/static/images/color_logo_transparent.svg' alt="eWalletBooster logo"/>
                        </a>    
                    <div 
                        role="button" 
                        className={this.state.active ? 'navbar-burger' : 'navbar-burger is-active'} 
                        onClick={this.toggleClass} 
                        aria-label="menu" 
                        aria-expanded="false" 
                        data-target='navMobile'
                        style={{backgroundColor: 'white'}}
                        >
                        <span aria-hidden="true"></span>
                        <span aria-hidden="true"></span>
                        <span aria-hidden="true"></span>                        
                    </div>          
                </div> 
                <div 
                    className={this.state.active ? 'navbar-menu': 'navbar-menu is-active'}>
                    <div className='container' style={{padding: '1rem', backgroundColor: '#003249'}}>
                        <aside className="menu">
                        <p className="menu-label" style={{color: 'white', textAlign: 'center'}}>
                            Account
                        </p>
                        <ul className="menu-list"> 
                            <li>
                                <Link href='/'>
                                <a style={linkStyle} onClick={this.toggleClass}>
                                    <span>
                                        <i style={{marginRight: '0.3rem'}} className='fa fa-home'></i>
                                    </span>Home
                                </a>
                                </Link>                                
                            </li>        
                        </ul>
                        <ul className="menu-list">
                            
                        </ul>

                        <p className="menu-label" style={{color: 'white', textAlign: 'center'}} onClick={this.toggleMenu}>
                            <span><i style={{marginRight: '0.3rem'}} className={!this.state.icon ? 'fa fa-plus-circle' : 'fa fa-minus-circle'}></i></span>Ewallets
                        </p>
                        <ul className="menu-list" style={{display: this.state.menu ? 'block' : 'none'}}> 
                            <MenuList brand={'Neteller'} link={'neteller'} toggleClass={this.toggleClass} /> 
                        </ul>
                        <ul className="menu-list" style={{display: this.state.menu ? 'block' : 'none'}}>  
                           <MenuList brand={'Skrill'} link={'skrill'} toggleClass={this.toggleClass} />   
                        </ul>
                        <ul className="menu-list" style={{display: this.state.menu ? 'block' : 'none'}}>
                           <MenuList brand={'Ecopayz'} link={'ecopayz'} toggleClass={this.toggleClass} />
                        </ul>               

                        <p className="menu-label" style={{color: 'white', textAlign: 'center'}}>
                            General
                        </p>
                        <ul className="menu-list">                            
                            <li>
                                <Link href='/contact'>
                                <a style={linkStyle} onClick={this.toggleClass}>
                                    <span>
                                        <i style={{marginRight: '0.3rem'}} className='fa fa-envelope'></i>
                                    </span>Contact
                                </a>
                                </Link>                                
                            </li>                            
                            {/* <li>
                                <Link href='/faq'>
                                <a style={linkStyle} onClick={this.toggleClass}>
                                    <span>
                                        <i style={{marginRight: '0.3rem'}} className='fa fa-question-circle'></i>
                                    </span>FAQ's
                                </a>
                                </Link>                                
                            </li>         */}
                        </ul>
                                 
                        </aside>
                    </div>
                </div>
            </div>
        )
    }
}





